				{ id: 2, title: '未消毒' }
			],
			dataSource2: [
				{ id: 1, title: '装箱' },
				{ id: 2, title: '不装箱' }
			],
			diskinds: ['A', "B", "C"],
			translates: ['红区', '绿区', '橙区'],
